.home-replays {
  h2 {
    text-align: center;
    margin-right: 0;
  }

  & + .page-highlight {
    z-index: 3;

    .block.edito {
      background-color: $lightgrey-3;
    }
    &::before {
      content: none;
    }

    & + .home-sectors {
      margin-top: -5%;
      margin-bottom: 0;

      &::after {
        left: 50%;
        transform: translate(-50%, -15%);
        height: 120%;

        @include mq_min_width($tablet-medium) {
          left: 50%;
        }
      }
    }
  }
}

.home-sectors {
  & + .home-search {
    clear: initial;
    @include clearfix;

    @include mq_min_width($tablet-medium) {
      margin-top: 80px;
    }
      
    & + div {
      width: 250px;
      height: 250px;
      margin-top: 49px;
      
      @include mq_min_width($tablet-medium) {
        float: right;
        margin-top: 80px;
      }
      
      & + .home-events {
        clear: both;
      }
    }
  }
}
