.detail-secteur.chiffres-cles {
  ul.detail-secteur__datas {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;
    padding: 30px;
    list-style: none;
    width: 100%;

    @include mq_min_width($desktop-small) {
      flex-direction: row;
      justify-content: space-between;
    }

    > li {
      flex-wrap: wrap;
      flex-direction: column;
      margin-left: auto;
      margin-right: auto;
      width: 100%;

      @include mq_min_width($desktop-small) {
        flex-direction: row;
        width: 33.3334%;
        max-width: 209px;
        min-width: 209px;
        margin-left: 15px;
        margin-right: 15px;
        margin-bottom: auto;
        padding: 0;
      }

      &::before {
        margin-right: 10px;
        
        @include mq_min_width($desktop-small) {
          margin-right: 0;
        }
      }

      > p {
        &:first-of-type {
          font-size: 34px;
          line-height: 1.12;
          justify-content: center;
        }
      }

      &:not(:last-of-type) {

        &::after {
          left: 0;
        }
        
        @include mq_min_width($tablet-medium) {
          &:after {
            content: none;
          }
        }
      }

      &.custom-icon {
        &-france::before {
          content: '\e912';
        }
        &-flag::before {
          content: '\e90f';
        }
        &-speaker::before {
          content: '\1f4e2';
        }
        &-micro::before {
          content: '\e910';
        }
      }
    }
  }
}