.home-images {
  position: relative;
  padding-top: 69px;

  &::after, 
  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    right: -25px;
    width: 100vw;
    height: 100%;
    background-color: $lightgrey-3;

    @include mq_min_width($tablet-medium) {
      right: 107px;
    }
  }

  .video-list__link {
    bottom: 10px;

    & + .video-list__link {
      display: none;
    }

    @include mq_min_width($tablet-medium) {
      bottom: initial;
      top: 13rem;
      left: 0;
      right: initial;

      & + .video-list__link {
        display: inline-block;
        left: 190px;
      }
    }

    @include mq_min_width($desktop-small) {
      top: 70px;
      left: initial;

      &:first-of-type {
        right: calc(28% + 20px);
      }
      & + .video-list__link {
        right: 12%;
        left: initial;
      }

    }
    @include mq_min_width($desktop-medium) {
      &:first-of-type {
        right: 28%;
      }
    }
  }

  & + .home-events {
    .main-title {
      @include mq_min_width($tablet-medium) {
        width: 100%;
        text-align: right;
      }
    }

    &::before {
      content: none;
    }
  }
}

.home-search-dark {
  position: relative;
  overflow: initial;
  padding: 71px 15px 72px;

  @include mq_min_width($tablet-medium) {
    padding-right: 0;
    padding-left: 8.3334%;
  }
}
