@import '../includes/variables';
@import '../includes/mixins';

.home-quicklinks {
  position: relative;
  z-index: 3;
  max-width: 1100px;
  @if $is_mobile {
    max-width: 500px;
  }
  margin: 0 auto 40px;

  @include mq_min_width($tablet-medium) {
    margin-bottom: 80px;
  }

  .hero + &,
  &.block {
    margin-top: -45px !important;
  }

  @extend .quicklinks-base;

  .ql-item {

    &.linkid794390 { // "Badge visiteurs" button
      .btn-primary {
        color: #fff;
        background-color: $theme-color-2;
      }
    }
    // "Pré-inscriptions visiteurs" button
    &.linkid814070 {
      @include mq_min_width($tablet-medium) {
        margin-left: auto;
        margin-right: 15px;
      }
    }
    // "Estimation pour exposer" button
    &.linkid814069 {
      @include mq_min_width($tablet-medium) {
        margin-left: 15px;
        margin-right: auto;
      }
    }
  }
}
