%greyBgnd {
  background-color: $lightgrey-3;
  content: '';
  display: block;
  position: absolute;
  top: -50%;
  left: 50%;
  width: 100vw;
  height: 150%;
  overflow: hidden;
  transform: translateX(-50%);
  z-index: -1;
}

body.pourquoi-exposer {
  .layout-2-col {
    position: relative;
    margin-bottom: 70px;

    &::before {
      @extend %greyBgnd;
    }

    #zone2 {
      width: 100%;
    }
  }
  .quicklinks {
    .icon-quote,
    .icon-home {
      &::before {
        @include icomoon;
      }
    }

    .icon-quote::before  {
      content: '\1f4ac';
    }
    .icon-home::before {
      content: '\e909';
    }
    .icon-exponent::before {
      content: '\e90a';
    }
  }

  .hero-why-exhibit {
    & + .quicklinks {
      margin-top: -50px;

      @include mq_min_width($tablet-medium) {   
        margin-top: 20px;   
        margin-left: 0;
        position: absolute;
        top: 200px;
        margin-bottom: 0;

        &::before {
          content: none;
        }
      }

      ul {
        @include mq_min_width($desktop-small) {
          flex-wrap: nowrap;
        }

        > li {
          width: 100%;

          @include mq_min_width($tablet-medium) {
            max-width: 290px;
          }

          &:first-child {
            margin-left: 0;
          }
        }
      }
    }
  }

  .breadcrumb-nav {
    position: relative;
  }

  .block.edito {
    .inside {
      position: relative;
      display: flex;
      padding: 20px 0;
      min-height: 656px;
      flex-direction: column;
            
      h2,
      h3 {
        @extend .main-title;
        color: $textcolor-3;
        order: 3;
        margin-top: 40px;
        margin-bottom: 25px;
        margin-left: 0;
      }

      h3 {
        order: 4;
      }
      
      > img {
        width: 100%;
        max-width: 426px;
        max-height: 656px;
        margin: auto;

        @include mq_min_width($tablet-medium) {
          width: 426px;
          height: 656px;
          margin: 0;
        }

        @include mq_min_width($desktop-small) {
          position: absolute;
          top: 20px;
          left: 0;
        }
      }

      @include mq_min_width($desktop-small) {      
        h2 {
          margin-top: 12px;
        }

        > *:not(img) {
          width: 50%;
          margin-left: auto;
          margin-right: 0;
        }

        &::after {
          position: absolute;
          content: '';
          top: 20px;
          left: 0;
          display: block;
          width: 426px;
          height: 656px;
          opacity: 0.5;
          background: linear-gradient(180deg, rgba(0,0,0,0.2) 0%, rgba(0,0,0,0.55) 100%);
        }
      }

      @include mq_min_width($desktop-medium) {
        > *:not(img) {
          width: 58.3334%;
          margin-left: auto;
          margin-right: 0;
        }
      }

      > p {
        margin-top: 0;

        &:first-of-type {
          order: 3;
        }

        &:nth-of-type(2) {
          order: 5;
        }
      }

      .detail-secteur {
        padding: 0;
        order: 1;

        @include mq_min_width($tablet-medium) {
          position: absolute;
          left: 426px;
          top: 20px;
        }

        @include mq_min_width($desktop-small) {
          position: relative;
          top: initial;
          left: initial;
          flex-wrap: wrap;
        }

        &__datas {
          max-width: 426px;
          margin: auto;

          @include mq_min_width($tablet-medium) {
            max-width: 250px;
          }
          @include mq_min_width($desktop-small) {
            max-width: initial;
          }
        }
      }

      .style1 {
        display: none;

        @include mq_min_width($tablet-medium) {
          margin-top: 0;
          text-align: center;
          position: absolute;
          top: 20px;
          left: 0;
          color: #fff;
          font-family: $font-2;
          font-size: 25px;
          font-weight: 500;
          line-height: 1.6;
          width: 100%;
          height: 100%;
          max-height: 656px;
          max-width: 426px;
          display: flex;
          flex-direction: column;
          justify-content: flex-end;
          z-index: 2;
          padding-bottom: 60px;
        }

        > strong {
          font-size: 120px;
          line-height: 0.5;
        }
      }
    }

    + .quicklinks {
      @include mq_min_width($tablet-medium) {
        margin-top: 90px;
      }
    }
  }

  #zone2 {
    > h2 {
      @extend .main-title;
      font-size: 25px;
      line-height: 1.6;
      color: $textcolor-2;
      position: relative;
    }
  }
}