.hero.hero-why-exhibit {
  height: 350px;
  z-index: 3;
  
  .hero {
    &__wrapper {
      margin-left: 0;
    }
    &__item {
      width: 75%;
    }

    &__background--with-img {
      background-color: $darkgrey-1;
      position: absolute;
      top: 0;
      left: 50%;
      display: block;
      width: 100vw;
      height: 100%;
      overflow: hidden;
      transform: translateX(-50%);
      z-index: -1;
    }
  }

  h1 {
    @extend .main-title;
    color: #fff;
    text-align: left;
  }
}