@import '~bourbon';
@import '../includes/variables';
@import '../includes/mixins';

.home-search,
.error-page {
  clear: both;
  margin-top: 49px;
  margin-bottom: 49px;
  overflow: hidden;

  @include mq_min_width($tablet-medium) {
    float: left;
    width: percentage(850px / $wrapper-size);
    margin-top: 100px;
    margin-bottom: 100px;
  }

  .main-title {
    margin-bottom: 40px;
  }

  .global-search-form {
    margin: 0 !important;
  }

  .gsf {
    &-fields {
      position: relative;
      padding: 0;
      border-radius: 5px;
      background-color: $lightgrey-3;
      overflow: hidden;
    }

    &-input {
      width: 100%;
      height: 100%;
      padding: 0 25px;
      background: none;
      border: 0;
    }

    &-submit {
      &-text {
        display: none;
      }

      &-btn {
        min-height: 64px;
        padding: 0 30px;
        color: $theme-color-2;
        background: none;

        .icon {
          display: block;
          font-size: 24px;
          color: currentColor;
          @include transition(.3s, transform);
        }

        &:hover {
          .icon {
            transform: scale(1.1);
          }
        }
      }
    }
  }


  .quicklinks {
    margin-top: 20px !important;
  }

  .ql-list {
    display: flex;
    margin: 0;
    flex-wrap: wrap;

    .btn-primary {
      @extend %button-underline;
      margin-right: 30px;
    }
  }

  &.is-ready .ql-item {
    opacity: 0;

    &.is-in-viewport { // animation
      animation: slideRightFadeIn 1.2s $transition-type forwards;

      @for $i from 1 through 10 {
        &:nth-child(#{$i}) {
          animation-delay: .1s * ($i - 1);
        }
      }
    }
  }
}

.home-search-dark {
  position: relative;

  .main-title,
  .txt-btn {
    color: $lightgrey-1;
  }

  &::before {
    content: '';
    position: absolute;
    z-index: -1;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100%;
    background-color: $textcolor-2;
  }

  &.home-search {
    .gsf-fields {
      background-color: rgba($lightgrey-1, 0.2);
    }
    .gsf-input {
      color: #fff;

      ::placeholder,
      :placeholder-shown {
        color: #fff;
      }
    }
  }
}